.sidebar{

    flex: 3;
    margin: 20px;
    padding-bottom: 30px;
    background-color: #fdfbfb;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.sidebarItem{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.sidebarTitle{
    margin: 10px;
    padding: 5px;
    width: 80%;
    border-top: 1px solid #a7a7a4;
    border-bottom: 1px solid #a7a7a4;
    font-family: 'Times New Roman', Times, serif;
    font-size: 12px;
    color: #222;
    font-weight: 600;
    line-height: 20px;
    text-align: center;
}

.sidebarItem > img{
    margin-top: 15px;

}

.sidebarItem > p{
    padding: 30px;
}

.sidebarList{
    list-style-type: none;
    margin-bottom: 30px;
}

.sidebarListItem{
    display: inline-block;
    width: 50px;
    margin-top: 15px;
    cursor: pointer;
}

.sidebarSocial {
    flex-wrap: nowrap;
    margin-top: 15px;
    width: 250px;
    display: flex;
    align-items: center;
    justify-content: center;

}

 .sidebarIcon{
     font-size: 16px;
     margin-left: 10px;
     cursor: pointer;
 }
 
 .sidebar > img{
     margin-top: 15px;
    
 }

 
.image{
    border: 1px solid #ddd;
    border-radius: 4px;
    padding: 5px;
    width: 150px;
}