.top{
    width: 100%;
    height: 50px;
    background-color: rgb(163, 101, 50);
    position: sticky;
    top: 0;
    display: flex;
    align-items: center;
    box-shadow: 500px;
    z-index: 999;

}

.topLeft,.topRight{
    flex: 3;
    display: flex;
    align-items: center;
    justify-content: center;
}

.topCenter{
    flex: 6;
}

.topIcon{
    font-size: 20px;
    margin-right: 10px;
    color: #444;
    cursor: pointer;

}

.topImg{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;

}

.Toplist{
    display: flex;
    justify-content: center;
    margin: 0;
    padding: 0;
    list-style: none; 

}

.TopListItem{
    margin-right: 20px;
    font-size: 18px;
    font-weight: 300;    
    cursor: pointer;

}

.topSrchIcon{
    font-size: 18px;
    color: rgb(102, 8, 8);   
    cursor: pointer;
    margin-left: 15px;
}

