.post{
    width: 385px;
    margin: 0px 25px 40px 25px;

}

.postImg{
    width: 100%;
    height: 280px;
    object-fit: cover;
    border-radius: px;
}

.postInfo{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.postCat{
    font-family: 'Courier New', Courier, monospace;
    font-size: 15px;
    color: #be9656;
    line-height: 20px;
    margin-top: 15px;
    margin-right: 10px;
    cursor: pointer;
}

.postTitle{
    font-family: 'Courier New', Courier, monospace;
    font-size: 24px;
    font-weight: 700;
    margin-top: 15px;
    cursor: pointer;
}

.postDate {
    font-family: "Lora", serif;
    font-style: italic;
    font-size: 13px;
    color: #999;
    margin-top: 15px;
}

.postDesc{
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 14px;
    color: #444;
    line-height: 24px;
    margin-top: 15px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 10;
    -webkit-box-orient: vertical;
    flex: 1;
    flex-wrap: wrap;
}

.image{
    border: 1px solid #ddd;
    border-radius: 10px;
    padding: 10px;
    align-items: center;
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 50%;

}

